import React from "react"
import {Button} from "@chakra-ui/react";
import {fidoLogin} from "../../Services/api";

const App = () => {

    const loginFido = async () => {
        const assertionOptions = await fidoLogin()

        console.log(assertionOptions)

        try {
            const cred = await navigator.credentials.get({
                publicKey: assertionOptions
            })

            console.log(cred)
        } catch (e) {
            console.error(e)
        }
    }

    return (
        <React.Fragment>
            <Button colorScheme='messenger' marginLeft='72%' marginTop='0.25rem'
                    onClick={loginFido}>
                FIDO 2
            </Button>
        </React.Fragment>
    )

}

export default App
