import React, {useState} from "react"
import withAuth from "../Services/WithAuth";
import {Button, Heading} from "@chakra-ui/react";
import {fidoAttestation, fidoRegister} from "../Services/api";

const App = () => {

    const registerFido = async () => {
        const attestationOptions = await fidoAttestation()

        const cred = await navigator.credentials.create({
            publicKey: attestationOptions
        })

        await fidoRegister(cred!)
    }

    return (
        <React.Fragment>

            <Heading>Register Fido2 Key</Heading>

            <Button onClick={registerFido}>Register!</Button>

        </React.Fragment>
    )

}

export default withAuth(App)
